h1 {
  text-align: center;
  margin-top: 50px;
  color: #ffd900;
  text-transform: uppercase;
  font-size: 60px;
}

img {
  display: block;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  border-radius: 50px;
  border: 15px solid #ffd900;
}

h2 {
  text-align: center;
  color: #ffd900;
  font-size: 30px;
}

h3 {
  text-align: center;
  color: #ffd900;
  font-size: 25px;
}

.App {
  font-family: "Courier New";
}

@media only screen and (max-width: 980px){
  img {
    width: 90%;
  }
}

